<template>
  <div>
    <div>I am in Home page</div>
    <div class="flexBox">
      <span>ref:{{ count }}</span>
      <span>store:{{ count }}</span>
      <span>double:{{ double }}</span>
      <button @click="addRef">addRef</button>
    </div>
    <ul>
      <li v-for="item in numbers" :key="item">{{ item }}</li>
    </ul>
    <h5>{{ person.name }}</h5>
    <div class="flexBox">
      <button @click="updateGreeting">updateTitle</button>
    </div>
  </div>
</template>

<script>
//ref valueref 
//  string  number   vuedefineProperty proxy  ref(3) data(2)
// reactive   reactive Proxyset forceUpdate
import { ref, reactive, toRefs, computed, watch } from "vue";
import { useStore } from 'vuex';
export default {
  //
  setup() {
    // store
    
    //
    // const count = ref(0);
    // const double = computed(() => {
    //   return count.value * 2;
    // });
    // const addRef = () => {
    //   count.value++;
    // };
    //
    const store = useStore();
    const data = reactive({
      count: 0,
      addRef: () => {
        data.count++;
        store.dispatch('add');
      },
      double: computed(() => data.count * 2),
      numbers: [1, 2, 3],
      person: {},
    });
    //
    data.numbers[3] = 5;
    data.person.name = "hahh";
    // watch
    const greetings = ref("");
    const updateGreeting = () => {
      greetings.value = "title";
    };
    watch([greetings, data], (newValue, oldValue) => {
      console.log({ new: newValue, old: oldValue });
      document.title = "update-" + greetings.value + "-" + data.count;
    });
    return {
      // count,
      // double,
      // addRef,
      ...toRefs(data),
      greetings,
      updateGreeting,
    };
  },
};
</script>

<style scoped>
.flexBox {
  width: 600px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>
